.page {
  position: relative;
  display: grid;
  grid-template: auto / 1fr 400px;
  column-gap: 20px;
  min-height: 100vh;
}

.page-content {
  padding: 20px;
}

.sidebar {
  position: sticky;
  top: 0;

  padding: 20px;
  border-left: 1px solid lightgray;
  height: 100vh;
}

iframe {
  display: none;
}
